"use client";

import { useAuthActions } from "@convex-dev/auth/react";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { useQuery } from "convex/react";
import { api } from "../../../../../convex/_generated/api";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "../../components/ui/card";
import { Input } from "../../components/ui/input";
import { Label } from "../../components/ui/label";
import { Button } from "../../components/ui/button";
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
  CommandList,
} from "../../components/ui/command";

export default function SignInPage() {
  const { signIn } = useAuthActions();
  const router = useRouter();
  const [step, setStep] = useState<"signUp" | "signIn">("signIn");
  const [isLoading, setIsLoading] = useState(false);
  const [organization, setOrganization] = useState("");
  const [orgSearch, setOrgSearch] = useState("");
  const [name, setName] = useState("");
  const [nameSearch, setNameSearch] = useState("");

  const authed = useQuery(api.auth.isAuthenticated, {});
  
  // Load employee profiles only when needed for DOPE Marketing selection
  const employeeProfiles = useQuery(
    api.employeeProfiles.getAllEmployeeProfiles,
    step === "signUp" && organization.trim().toLowerCase() === "dope marketing" ? {} : "skip"
  );

  useEffect(() => {
    if (authed === true) router.push("/agents");
  }, [authed, router]);

  if (authed === true) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary" />
      </div>
    );
  }

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setIsLoading(true);
    try {
      const formData = new FormData(event.currentTarget);
      await signIn("password", formData);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center px-4 bg-background">
      <div className="w-full max-w-md">
        <Card>
          <CardHeader className="space-y-1">
            <CardTitle className="text-2xl text-center">
              {step === "signIn" ? "Welcome back" : "Create account"}
            </CardTitle>
            <CardDescription className="text-center">
              {step === "signIn" 
                ? "Sign in to your account to continue" 
                : "Enter your information to create your account"
              }
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input 
                  id="email" 
                  name="email" 
                  type="email" 
                  placeholder="m@example.com"
                  required 
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="password">Password</Label>
                <Input 
                  id="password" 
                  name="password" 
                  type="password" 
                  required 
                />
              </div>
              {step === "signUp" && (
                <div className="space-y-2">
                  <Label>Organization</Label>
                  <div className="border rounded-lg">
                    <Command>
                      <CommandInput
                        placeholder="Search or type your organization..."
                        value={orgSearch}
                        onValueChange={setOrgSearch}
                      />
                      <CommandList>
                        <CommandEmpty>No results found.</CommandEmpty>
                        {orgSearch && (
                          <CommandGroup heading="Custom">
                            <CommandItem onSelect={() => { setOrganization(orgSearch); setOrgSearch(orgSearch); }}>
                              Use "{orgSearch}" as your organization
                            </CommandItem>
                          </CommandGroup>
                        )}
                        <CommandGroup heading="Suggestions">
                          <CommandItem onSelect={() => { setOrganization("DOPE Marketing"); setOrgSearch("DOPE Marketing"); }}>
                            DOPE Marketing
                          </CommandItem>
                        </CommandGroup>
                      </CommandList>
                    </Command>
                  </div>
                  {organization && (
                    <p className="mt-2 text-xs text-muted-foreground">
                      Selected: <span className="font-semibold">{organization}</span>
                    </p>
                  )}
                  <input type="hidden" name="organization" value={organization} />
                </div>
              )}
              {step === "signUp" && (
                <div className="space-y-2">
                  <Label>Name</Label>
                  {organization.trim().toLowerCase() === "dope marketing" ? (
                    <>
                      <div className="border rounded-lg">
                        <Command>
                          <CommandInput
                            placeholder="Search your name..."
                            value={nameSearch}
                            onValueChange={setNameSearch}
                          />
                          <CommandList>
                            <CommandEmpty>No results found.</CommandEmpty>
                            <CommandGroup heading="Employees">
                              {(employeeProfiles || [])
                                .filter((p) => (p.organization || "").trim().toLowerCase() === "dope marketing")
                                .filter((p) =>
                                  nameSearch.trim() === ""
                                    ? true
                                    : p.name.toLowerCase().includes(nameSearch.trim().toLowerCase()),
                                )
                                .map((p) => (
                                  <CommandItem
                                    key={p._id as unknown as string}
                                    onSelect={() => {
                                      setName(p.name);
                                      setNameSearch(p.name);
                                    }}
                                  >
                                    {p.name}
                                  </CommandItem>
                                ))}
                            </CommandGroup>
                          </CommandList>
                        </Command>
                      </div>
                      {name && (
                        <p className="mt-2 text-xs text-muted-foreground">
                          Selected: <span className="font-semibold">{name}</span>
                        </p>
                      )}
                      <input type="hidden" name="name" value={name} />
                    </>
                  ) : (
                    <Input
                      id="name"
                      name="name"
                      type="text"
                      placeholder="Your full name"
                      required
                    />
                  )}
                </div>
              )}
              <input name="flow" type="hidden" value={step} />
              <Button type="submit" disabled={isLoading} className="w-full">
                {isLoading ? (
                  <div className="flex items-center gap-2">
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary-foreground" />
                    {step === "signIn" ? "Signing in..." : "Creating account..."}
                  </div>
                ) : (
                  step === "signIn" ? "Sign in" : "Create account"
                )}
              </Button>
            </form>

            <div className="mt-6 text-center">
              <Button 
                variant="ghost" 
                type="button" 
                onClick={() => setStep(step === "signIn" ? "signUp" : "signIn")} 
                className="text-sm"
              >
                {step === "signIn" 
                  ? "Don't have an account? Sign up" 
                  : "Already have an account? Sign in"
                }
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}



